:root {
  --bg-primary: #0a0a0a;
  --bg-secondary: #111111;
  --text-primary: #ffffff;
  --text-secondary: #888888;
  --accent: #00ff88;
  --accent-secondary: #00ccff;
  --gradient-1: linear-gradient(135deg, #00ff88 0%, #00ccff 100%);
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Inter', sans-serif;
  background: var(--bg-primary);
  color: var(--text-primary);
  overflow-x: hidden;
  cursor: none;
}

/* Selection */
::-moz-selection {
  background: var(--accent);
  color: var(--bg-primary);
}
::selection {
  background: var(--accent);
  color: var(--bg-primary);
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: var(--bg-secondary);
}

::-webkit-scrollbar-thumb {
  background: var(--accent);
  border-radius: 4px;
}

/* Custom Cursor Styles */
.cursor-dot {
  width: 8px;
  height: 8px;
  background: var(--accent);
  border-radius: 50%;
  position: fixed;
  pointer-events: none;
  z-index: 99999;
  mix-blend-mode: difference;
  transition: transform 0.1s ease;
}

.cursor-outline {
  width: 40px;
  height: 40px;
  border: 2px solid var(--accent);
  border-radius: 50%;
  position: fixed;
  pointer-events: none;
  z-index: 99998;
  transition: all 0.15s ease-out;
  mix-blend-mode: difference;
}

.cursor-outline.hover {
  transform: scale(1.5);
  background: rgba(0, 255, 136, 0.1);
  border-color: var(--accent-secondary);
}

.cursor-outline.clicking {
  transform: scale(0.8);
}

.cursor-text {
  position: fixed;
  pointer-events: none;
  z-index: 99997;
  font-family: 'Space Grotesk', monospace;
  font-size: 10px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--accent);
  opacity: 0;
  transition: opacity 0.3s ease;
}

.cursor-text.visible {
  opacity: 1;
}

/* Magnetic Button Effect */
.magnetic {
  transition: transform 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

/* Page Transitions */
.page-transition {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 9999;
  pointer-events: none;
}

.transition-panel {
  position: absolute;
  width: 100%;
  height: 25%;
  background: var(--accent);
  transform: scaleY(0);
  transform-origin: bottom;
}

.transition-panel:nth-child(1) { top: 0; }
.transition-panel:nth-child(2) { top: 25%; }
.transition-panel:nth-child(3) { top: 50%; }
.transition-panel:nth-child(4) { top: 75%; }

/* Loader */
.loader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--bg-primary);
  z-index: 10000;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;
}

.loader-logo {
  font-family: 'Syne', sans-serif;
  font-size: clamp(4rem, 15vw, 10rem);
  font-weight: 800;
  background: var(--gradient-1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  opacity: 0;
  transform: scale(0.8);
}

.loader-progress-container {
  width: 300px;
  height: 4px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}

.loader-progress {
  height: 100%;
  background: var(--gradient-1);
  border-radius: 10px;
  width: 0%;
}

.loader-percent {
  font-family: 'Space Grotesk', monospace;
  font-size: 1rem;
  color: var(--text-secondary);
  letter-spacing: 3px;
}

/* Section Reveal Animations */
.section-slide {
  clip-path: inset(0 0 100% 0);
}

.section-slide.revealed {
  clip-path: inset(0 0 0 0);
}

/* Text Split Animation */
.split-text .char {
  display: inline-block;
  opacity: 0;
  transform: translateY(100px) rotate(10deg);
}

.split-text.animated .char {
  opacity: 1;
  transform: translateY(0) rotate(0);
}

/* Stagger Reveal */
.stagger-reveal > * {
  opacity: 0;
  transform: translateY(60px);
}

/* Parallax */
.parallax {
  will-change: transform;
}

/* Floating Animation */
@keyframes float {
  0%, 100% { transform: translateY(0px); }
  50% { transform: translateY(-20px); }
}

.floating {
  animation: float 6s ease-in-out infinite;
}

/* Gradient Text */
.gradient-text {
  background: var(--gradient-1);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

/* Glow Effect */
.glow {
  box-shadow: 0 0 60px rgba(0, 255, 136, 0.3);
}

.glow-text {
  text-shadow: 0 0 40px rgba(0, 255, 136, 0.5);
}

/* Noise Overlay */
.noise::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("data:image/svg+xml,%3Csvg viewBox='0 0 400 400' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E");
  opacity: 0.03;
  pointer-events: none;
  z-index: 1;
}

/* Marquee */
.marquee-track {
  display: flex;
  animation: marquee 25s linear infinite;
}

.marquee-track:hover {
  animation-play-state: paused;
}

@keyframes marquee {
  0% { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}

/* Smooth Image Reveal */
.image-reveal {
  overflow: hidden;
  position: relative;
}

.image-reveal::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--accent);
  transform: scaleX(1);
  transform-origin: right;
  transition: transform 1s cubic-bezier(0.77, 0, 0.175, 1);
}

.image-reveal.revealed::after {
  transform: scaleX(0);
}

/* Border Animation */
@keyframes borderRotate {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

.animated-border {
  position: relative;
  overflow: hidden;
}

.animated-border::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: conic-gradient(from 0deg, transparent, var(--accent), transparent 30%);
  animation: borderRotate 4s linear infinite;
}

.animated-border::after {
  content: '';
  position: absolute;
  inset: 3px;
  background: var(--bg-secondary);
  border-radius: inherit;
}

/* Button Styles */
.btn-primary {
  position: relative;
  padding: 18px 45px;
  background: var(--gradient-1);
  border: none;
  border-radius: 50px;
  font-family: 'Space Grotesk', monospace;
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--bg-primary);
  cursor: none;
  overflow: hidden;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.btn-primary::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
  transition: left 0.5s ease;
}

.btn-primary:hover::before {
  left: 100%;
}

.btn-primary:hover {
  transform: scale(1.05);
  box-shadow: 0 20px 50px rgba(0, 255, 136, 0.4);
}

.btn-secondary {
  padding: 18px 45px;
  background: transparent;
  border: 2px solid rgba(255,255,255,0.3);
  border-radius: 50px;
  font-family: 'Space Grotesk', monospace;
  font-size: 0.85rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: var(--text-primary);
  cursor: none;
  transition: all 0.3s ease;
}

.btn-secondary:hover {
  border-color: var(--accent);
  color: var(--accent);
  transform: scale(1.05);
  box-shadow: 0 0 30px rgba(0, 255, 136, 0.2);
}

/* Responsive */
@media (max-width: 768px) {
  body {
    cursor: auto;
  }
  
  .cursor-dot,
  .cursor-outline,
  .cursor-text {
    display: none;
  }
}
